@media screen and (max-width: 1600px) {
  /*header*/
  .main_head {
    .head_menu {
      max-width: none;
      position: absolute;
    }
  }
  /*END HEADER*/

}

@media screen and (max-width: 1500px) {
  .main_footer {
    .footer_head {
      padding: rem(100px) 0 rem(125px) 20%;
    }
  }
}

@media screen and(max-width: 1360px) {
  /* BEGIN HOME CONTENT*/
  .home_content {
    .content_menu {
      height: auto;
      margin-top: 0;
      .list_menu {
        flex-wrap: wrap;
        border: 1px solid green;
        .list_item {
          border: solid rem(20px) #fff;
          height: rem(300px);
          flex: 1 1 35%;
          max-width: 50%;
          top: auto;
          box-shadow: 0 0 rem(5px) rem(7px) rgba(255, 255, 255, 1);
          .item {
            width: 80%;
            height: 80%;
            margin: auto;
            top: 0;
            right: 0;
            right: 0;
            bottom: 0;
            display: flex;
            position: absolute;
            a {
              transform: rotate(0);
              width: auto;
              height: rem(80px);
              line-height: rem(76px);
              padding: 0 rem(40px);
              top: auto;
              left: auto;
              position: relative;
              margin: auto;
              right: auto;
              bottom: auto;
            }

          }
        }
      }
    }
  }
  /*END HOME CONTENT*/
}

@media screen and (max-width: 1280px) {
  .main_footer {
    .footer_head {
      padding: rem(30px) 0 rem(30px) 0;
      align-items: center;
      flex-direction: column;
      .footer_contacts {
        order: 1;
        span {
          margin-bottom: 0;
        }
      }
      .footer_menu {
        margin-bottom: rem(30px);
        flex-direction: row;
        a {
          margin-left: rem(30px);
        }
      }
      .footer_logo {
        display: none;
      }
    }

  }
}

@media screen and (max-width: 1000px) {
  .main_head {
    .head_menu {
      padding: 0 rem(25px) 0 rem(25px);
      display: flex;
      justify-content: space-between;
      .menu_nuv {
        position: absolute;
        right: 0;
        top: 70%;
        visibility: hidden;
        transition: .4s;
        opacity: 0;

        width: rem(200px);
        height: rem(150px);
        background-color: #000000;

        ul {
          display: flex;
          flex-direction: column;
          align-items: center;
          li {
            margin-left: 0;
            &::before {
              @include before_fig(100%, rem(2px), $blue_color);
              top: 25px;
            }
          }
        }
        &.active {
          top: 100%;
          opacity: 1;
          visibility: visible;
        }
      }
      .right_menu {
        flex-direction: row-reverse;

        .btn_mobile {
          margin-left: rem(20px);
          display: flex;
          &.active {
            .btn1 {
              transform: rotate(-411deg) translate(-15px, 1px);;
            }
            .btn2 {
              opacity: 0;
            }
            .btn3 {
              transform: rotate(412deg) translate(-11px, 2px);

            }
          }
        }
      }
    }
  }
}

/*footer*/
@media screen and (max-width: 800px) {
  .feedback_section {
    .feedback_wrap {
      padding: rem(25px);
      flex-direction: column;
      .feedback_text {
        width: 100%;
        text-align: center;
        p {
          margin: rem(20px) 0 rem(20px) 0;
        }
      }
      .feedback_form {
        width: 100%;
        max-width: none;
        text-align: center;
      }
    }
  }
}

@media screen and (max-width: 500px) {
  /*footer*/
  .main_footer {
    .footer_head {
      .footer_menu {

        a {
          font-size: rem(15px);
          &:first-child {
            margin-left: 0;
          }
        }
      }
    }
    .footer_contacts {
      font-size: rem(15px);
    }
  }
  /*END FOOTER*/
  /*feedback*/
  .feedback_section {
    .feedback_wrap {
      padding: rem(5px);
      h2 {
        font-size: rem(25px);
      }
      p {
        font-size: rem(15px);
      }
      .feedback_form {
        padding: rem(50px) rem(50px);
        h2 {
          font-size: rem(30px);
        }
      }
    }
  }

  /*END FEEDBACK*/
}





